.container {
  max-width: 108rem;
  margin: 0 auto;
  padding: 0 3rem;
  box-sizing: content-box;

  @media (max-width: $laptop) {
    min-width: 67.8rem;
    padding: 0 4.5rem;
  }

  @media (max-width: $tablet) {
    min-width: initial;
    max-width: initial;
    padding: 0 2rem;
  }
}

.container.header__container {
  max-width: 115rem;
}
